import React from 'react';
import './style.css';

function ContactForm() {
    return(
    <section className="contact_form">
        <div className="contact_form_block">
		    <div className="contact_form_info">
			    <h1>Контактна інформація:</h1>
			    <p>Якщо ви обрали послугу для замовлення. 
					Будь-ласка, заповніть форму на правій стороні
				</p>
			    <span><b>Адреса:</b> Хмельницька обл., м.Деражня, вулиця Миру 55/1</span>
			    <span><b>Телефон:</b>  +38 038 563 01 55</span>
			    <span><b>Електронна пошта:</b>  oksamyt@gmail.com</span>
		    </div>
            <div className="forms">
			    <input type="text" className="name" name="name" placeholder="Ваше ім'я" id="your-name"></input>
			    <input type="email" className="email" name="email" placeholder="Ваша електронна пошта" id="email"></input>
			    <input type="text" className="title" name="title" placeholder="Назва замовлення" id="company-name"></input>
			    <textarea name="textarea" className="textarea" id="textarea" placeholder="Опис замовлення" cols="50" rows="10"></textarea>
			    <input className="button" type="submit" value="Відправити"></input>
		    </div>
        </div>
	</section>
    )
}
export default ContactForm;